import {
  Box,
  Button,
  Container,
  FormControl,
  InputLabel,
  makeStyles,
  MenuItem,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@material-ui/core";
import { Form, Formik } from "formik";
import React, { useEffect, useLayoutEffect, useState } from "react";
import Balance from "../../../../components/Balance";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { useDispatch, useSelector } from "react-redux";
import {
  getListDeals,
  getMyDeals,
  setDeleteDeal,
} from "../../../../redux/reducers/P2P";
import { Link } from "react-router-dom";
import success from "../../../../assets/images/state/success.svg";
import NoneResults from "../../History/NoneResults";
import { getСurrenciesList } from "../../../../redux/reducers/Сurrencies";
import DeleteIcon from "@mui/icons-material/Delete";
import { Alert } from "@mui/material";
import DeleteDeal from "./deleteDeals";
const Deals = ({ t }) => {
  const dispatch = useDispatch();

  const classes = useStyles();

  const { listDels, myDeals, answerDeletedDeals } = useSelector(
    ({ P2PReducer }) => P2PReducer
  );
  const { СurrenciesList } = useSelector(
    ({ СurrenciesReducer }) => СurrenciesReducer
  );
  const { profileData } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  useEffect(() => {
    dispatch(getListDeals());
    dispatch(getMyDeals());
    // dispatch(getСurrenciesList());
    // eslint-disable-next-line
  }, []);

  const [filterData, setFilterData] = useState([]);
  const filter = (values) => {
    return setFilterData(
      listDels
        .filter((item) =>
          values?.type === "sell"
            ? item?.sell_currency === values?.crypto
            : item?.buy_currency === values?.crypto
        )
        .filter((item) =>
          values?.type === "sell"
            ? values.AmountFrom < item.sell_quantity
            : values.AmountFrom < item.buy_quantity
        )
    );
  };

  useEffect(() => {
    setFilterData(listDels);
    // eslint-disable-next-line
  }, [listDels]);

  const sortArr =
    Array.isArray(myDeals) &&
    myDeals.sort(function (a, b) {
      return (
        new Date(b.create_at * 1000).getTime() -
        new Date(a.create_at * 1000).getTime()
      );
    });

  const [deleteModal, setDeleteModal] = useState(false);
  const [deleteId, setDeleteId] = useState(0);

  const deleteDealById = (id) => {
    setDeleteModal(true);
    setDeleteId(id);
  };

  const firstUpdate = React.useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }
    if (answerDeletedDeals.severity === 202) {
      dispatch(getСurrenciesList());
      dispatch(getMyDeals());
      setTimeout(() => {
        setDeleteModal(false);
      }, 500);
    }
    // eslint-disable-next-line
  }, [answerDeletedDeals.severity]);

  return (
    <section style={{ color: "#000" }}>
      <Snackbar
        open={answerDeletedDeals.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => dispatch(setDeleteDeal({ ...alert, open: false }))}
      >
        <Alert
          onClose={() => dispatch(setDeleteDeal({ ...alert, open: false }))}
          severity={answerDeletedDeals.severity === 202 ? "success" : "error"}
          variant="filled"
        >
          {answerDeletedDeals.message}
        </Alert>
      </Snackbar>
      <Container>
        <Balance />
        <Box className={classes.DealsBox}>
          <Typography
            className={classes.DealsTitle}
            style={{ marginBottom: 20 }}
          >
            {t("ListDeals")}
          </Typography>
          <Formik
            initialValues={{}}
            onSubmit={(values) => {
              filter(values);
            }}
          >
            {({ values, handleChange }) => (
              <Form className={classes.Form}>
                <FormControl className={classes.formControl}>
                  <InputLabel>{t("TranslationMethod")}</InputLabel>
                  <Select
                    fullWidth
                    label={t("TranslationMethod")}
                    name="type"
                    variant="outlined"
                    value={values.condition}
                    onChange={handleChange}
                    required
                  >
                    <MenuItem value={"sell"}>{t("sell")}</MenuItem>
                    <MenuItem value={"buy"}>{t("buy")}</MenuItem>
                  </Select>
                </FormControl>

                <FormControl className={classes.formControl}>
                  <InputLabel>{t("Cryptocurrency")}</InputLabel>
                  <Select
                    fullWidth
                    label={t("Cryptocurrency")}
                    name="crypto"
                    variant="outlined"
                    value={values.condition}
                    onChange={handleChange}
                    required
                  >
                    {СurrenciesList &&
                      СurrenciesList.map((item, index) => {
                        return (
                          <MenuItem value={item.currency} key={index}>
                            <img src={item.logo} alt="" />
                            {item.name}
                          </MenuItem>
                        );
                      })}
                  </Select>
                </FormControl>
                <FormControl className={classes.formControl}>
                  <TextField
                    label={t("AmountFrom")}
                    type="text"
                    variant="outlined"
                    name="AmountFrom"
                    value={values.AmountFrom}
                    onChange={handleChange}
                    required
                  />
                </FormControl>
                <FormControl className={classes.formControl}>
                  <TextField
                    label={t("AmountTo")}
                    type="text"
                    variant="outlined"
                    name="AmountTo"
                    value={values.AmountTo}
                    onChange={handleChange}
                    required
                  />
                </FormControl>
                <Button
                  type="submit"
                  size="large"
                  style={{ height: "auto" }}
                  variant="contained"
                  color="primary"
                  className={classes.button}
                >
                  <svg
                    style={{ marginRight: 10 }}
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.66683 13.9997C11.1646 13.9997 14.0002 11.1641 14.0002 7.66634C14.0002 4.16854 11.1646 1.33301 7.66683 1.33301C4.16903 1.33301 1.3335 4.16854 1.3335 7.66634C1.3335 11.1641 4.16903 13.9997 7.66683 13.9997Z"
                      stroke="black"
                      stroke-width="1.5"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                    <path
                      d="M14.6668 14.6663L13.3335 13.333"
                      stroke="black"
                      stroke-width="1.5"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    />
                  </svg>
                  {t("Search")}
                </Button>
              </Form>
            )}
          </Formik>
        </Box>
        <Typography className={classes.DealsTitle}>
          {t("p2pDealsTitle")}
        </Typography>
        <Box className={classes.DealsBox}>
          {!filterData.length ? (
            <NoneResults t={t} p2p />
          ) : (
            <TableContainer component={Paper} className={classes.DealsTAble}>
              <Table
                sx={{ minWidth: 650 }}
                aria-label="simple table"
                size="small"
              >
                <TableHead>
                  <TableRow>
                    <TableCell align="center">{t("GetIt")}</TableCell>
                    <TableCell align="center">{t("Currency")}</TableCell>
                    <TableCell align="center">{t("GiveBack")}</TableCell>
                    <TableCell align="center">{t("Currency")}</TableCell>
                    <TableCell align="center">{t("TypeDeal")}</TableCell>
                    <TableCell align="center">{t("action")}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {Array.isArray(filterData) &&
                    filterData.map((row) => (
                      <TableRow
                        key={row.name}
                        sx={{
                          "&:last-child td, &:last-child th": { border: 0 },
                        }}
                      >
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          {row.sell_quantity_with_commission
                            ? row.sell_quantity_with_commission
                            : row.sell_quantity}
                        </TableCell>
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          {row.sell_currency}
                        </TableCell>
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          {row.type === "card2" || "cash2"
                            ? row.buy_quantity
                            : row.buy_quantity_with_commission}
                        </TableCell>
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          {row.buy_currency}
                        </TableCell>
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          {t(
                            row.type === "cash" || row.type === "cash2"
                              ? "meeting"
                              : row.type === "card" || row.type === "card2"
                              ? "bankCard"
                              : "bankCard"
                          )}
                        </TableCell>
                        <TableCell
                          className={classes.tableCellContent}
                          align="center"
                        >
                          <Link to={`/dashboard/p2p/joinDeals/${row.id}`}>
                            <Button
                              variant="contained"
                              color="primary"
                              size="large"
                            >
                              {t("More")}
                            </Button>
                          </Link>
                        </TableCell>
                      </TableRow>
                    ))}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </Box>
        <Typography variant="h2" className={classes.DealsTitle}>
          {t("myDeals")}
        </Typography>
        <Box className={classes.DealsBox}>
          <TableContainer component={Paper} className={classes.DealsTAble}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="center">{t("GiveBack")}</TableCell>
                  <TableCell align="center">{t("Currency")}</TableCell>
                  <TableCell align="center">{t("GetIt")}</TableCell>
                  <TableCell align="center">{t("Currency")}</TableCell>
                  <TableCell align="center">{t("TypeDeal")}</TableCell>
                  <TableCell align="center">{t("state")}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {Array.isArray(sortArr) &&
                  sortArr.map((row) => (
                    <TableRow
                      key={row.name}
                      sx={{
                        "&:last-child td, &:last-child th": { border: 0 },
                      }}
                    >
                      <TableCell align="center">
                        {row.participant === profileData.data?.profile?.email
                          ? row.type === "cash2" || row.type === "card2"
                            ? row.buy_quantity
                            : row.buy_quantity
                          : row.type === "cash2" || row.type === "card2"
                          ? row.sell_quantity
                          : row.sell_quantity}
                      </TableCell>
                      <TableCell align="center">
                        {row.participant === profileData.data?.profile?.email
                          ? row.buy_currency
                          : row.sell_currency}
                      </TableCell>
                      <TableCell align="center">
                        {row.participant === profileData.data?.profile?.email
                          ? row.type === "cash2" || row.type === "card2"
                            ? row.sell_quantity
                            : row.sell_quantity_with_commission
                          : row.type === "cash2" || row.type === "card2"
                          ? row.buy_quantity_with_commission
                          : row.buy_quantity}
                      </TableCell>
                      <TableCell align="center">
                        {row.participant === profileData.data?.profile?.email
                          ? row.sell_currency
                          : row.buy_currency}
                      </TableCell>
                      <TableCell align="center">
                        {t(
                          row.type === "cash" || row.type === "cash2"
                            ? "meeting"
                            : row.type === "card" || row.type === "card2"
                            ? "bankCard"
                            : "bankCard"
                        )}
                      </TableCell>
                      <TableCell align="center">
                        {row.status === "process" ||
                        row.status === "expectation" ? (
                          <>
                            <Link to={`/dashboard/p2p/joinDeals/${row.id}`}>
                              <Button
                                variant="contained"
                                color="primary"
                                size="large"
                              >
                                {t(
                                  row.status === "process"
                                    ? "inProgress"
                                    : "More"
                                )}
                              </Button>
                            </Link>
                            {row.status === "expectation" && (
                              <Button
                                style={{
                                  marginLeft: 5,
                                  backgroundColor: "#fcac45",
                                }}
                                onClick={() => deleteDealById(row.id)}
                              >
                                <DeleteIcon />
                              </Button>
                            )}
                          </>
                        ) : (
                          <img
                            className={classes.DealsIcon}
                            src={success}
                            alt="icon"
                          />
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
        <DeleteDeal
          t={t}
          open={deleteModal}
          setOpen={setDeleteModal}
          id={deleteId}
        />
      </Container>
    </section>
  );
};

export default Deals;

const useStyles = makeStyles((theme) => ({
  DealsBox: {
    paddingBottom: 40,
    marginBottom: 40,
    borderBottom: "2px solid #E7EAEE",
    [theme.breakpoints.down("md")]: {
      paddingBottom: 16,
      marginBottom: 16,
    },
  },
  DealsForm: {
    display: "flex",
    alingnItems: "center",
    backgroundColor: "#fff",
    borderRadius: 8,
    padding: 16,
    columnGap: 12,
  },
  Form: {
    display: "flex",
    alingnItems: "center",
    backgroundColor: "#fff",
    borderRadius: 8,
    padding: 16,
    columnGap: 12,
    [theme.breakpoints.down("md")]: {
      flexWrap: "wrap",
    },
  },

  formControl: {
    backgroundColor: "#F7F8F9",

    [theme.breakpoints.down("md")]: {
      marginBottom: 16,
    },
    "& .MuiSelect-root": {
      border: "none!important",
    },
    "& .MuiInputLabel-formControl": {
      left: 10,
    },

    "& .MuiInputLabel-shrink": {
      left: 0,
    },
  },

  formSelect: {
    backgroundColor: "#fff",
    "& .MuiSelect-root": {
      border: "none!important",
    },
    "& .MuiInputLabel-formControl": {
      left: 16,
    },
  },
  button: {
    width: 170,
    display: "flex",
    alignItems: "center",
    [theme.breakpoints.down("md")]: {
      width: "100%",
      height: "46px!important",
    },
  },
  DealsTAble: {
    borderRadius: "12px !important",
    boxShadow: "0px 1px 0px #E2E8F0 !important",
    marginTop: 40,
    [theme.breakpoints.down("md")]: {
      marginTop: 16,
    },
  },
  DealsTitle: {
    color: "#000",
    fontSize: 23,
    [theme.breakpoints.down("md")]: {
      fontSize: 19,
    },
  },
  DealsSupTitle: {
    color: "#64748B",
    marginBottom: 24,
    fontWeight: 300,
  },
  DealsImg: {
    width: 145,
    height: 40,
    objectFit: "contain",
  },
  DealsIcon: {
    width: 51,
    height: 44,
  },
}));
